<template>
  <div id="history">
    <header-nav :title="msg"></header-nav>
    <news-list
    :deleteBol="false"
    :name="2"
    :article_check="true"
    :isHistory="1"
    :list="list"
    @deleteFn="deleteFn"
    :readLineBol="true"
    :gray="false"
    v-if="list.length !== 0"></news-list>
    <div class="no-more-data" v-if="this.list.length > 0">没有更多数据了！</div>
    <div id="noList" v-if="this.list.length === 0">
      <stateless-head :headData="headData"></stateless-head>
      <news-list :list="recommendList"></news-list>
    </div>
  </div>
</template>

<script>
import HeaderNav from '../common/header-nav'
import NewsList from '../common/news-list'
import StatelessHead from '../common/stateless-head'

import { mapState } from 'vuex'

export default {
  name: 'history',
  computed: mapState({
    commonState: state => state.common
  }),
  data () {
    return {
      msg: '历史阅读',
      list: [],
      headData: {
        imgUrl: require('../../assets/img/icon-history.png'),
        txt: '没有历史阅读'
      },
      recommendList: []
    }
  },
  methods: {
    // 删除历史记录
    deleteFn (id) {
      let data = {}
      for (let [i, v] of this.list.entries()) {
        if (v.id === id) {
          this.list.splice(i, 1)
          this.$utils.cache('historys', JSON.stringify(this.list))
          break
        }
      }
      data = this.$utils.cache('history') ?  JSON.parse(this.$utils.cache('history')) : data
      delete data[id]
      this.$utils.cache('history', JSON.stringify(data))
      this.$toast({
        message: '删除成功',
        position: 'middle',
        duration: 1500
      })
      this.getList()
    },
    // 获取历史记录
    getList () {
      if (this.$utils.cache('historys')) {
        let data = JSON.parse(this.$utils.cache('historys'))
        this.list = data
      } else {
        this.getRecommend()
      }
    },
    // 上拉函数
    upList () {
      if (this.$utils.scrollHeight() === 0) {
        let data = {}
        data.columnid = 'index'
        data.lastid = this.recommendList[(this.recommendList.length - 1)].id
        this.$api.get('PULL_UP', data, (res) => {
          let list = res.data || []
          this.recommendList = [...this.recommendList, ...list]
        })
      }
    },
    // 获取推荐阅读
    getRecommend () {
      let data = {}
      data.columnid = 'index'
      this.$api.get('PULL', data, (res) => {
        this.recommendList = res.data || []
        window.onscroll = this.upList
      })
    }
  },
  mounted () {
    this.getList()
  },
  components: {
    HeaderNav,
    NewsList,
    StatelessHead
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" type="text/css">
</style>



